	module.exports = {"data":{"markdownRemark":{"html":"<p>この2日間で、私は「中国の偉大な歴史」を読んで、何千年もの間、中国の歴史を書けない言葉で書きました。 「偉大な歴史」は「マクロ歴史」であり、「MACROHISTORY」とは関係ありませんが、それが大きな歴史であれば、ヤン・ギーフェイについて書くことは非常に奇妙であり、そのような洗練された歴史においても、そのような人々も地位を持っています。しかし、この本は中国史研究にとって有益である。彼の基本的な考え方は：</p>\n<p>権力を有効に利用しなければ、安定と統一はできない。</p>\n<p>歴史的な見地からすれば、中国における最も重要な問題は農民を統一することであり、農家を管理する仕組みがなければ中国社会は混乱するだろう。</p>\n<p>財政は、中国の封建時代の安定や盛衰の重要な要素であり、財政と税制がなければ、中央政府は安定して存在することはできない。</p>\n<p>もう一つの重要な要素は土地管理であり、土地と農民はつながっている。合理的な土地制度はなく、農業経営者は議論することができない。</p>\n<p>公式の制度も非常に重要です。過去の世代の政府のルールは、強い官僚に頼っており、官僚はそれを行うことができませんでした。</p>\n<p>イデオロギーはまた、中国の安定にとっても重要であり、中国の制度力はこれまで決して強くはなかったため、イデオロギーやマネジメントの道徳的要因に依存しており、社会管理における儒教の地位を常に維持できる基本的な理由である。</p>\n<p>公職者の腐敗は、常に王朝の衰退の基本的な理由であり、中国社会の歴史的発展がこれを証明している。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0116.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0116.md","frontmatter":{"title":"6月23日 木曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0116.md absPath of file >>> MarkdownRemark"}}

